.wrraper {
    box-sizing: border-box;
    box-shadow: 0px 3px 100px rgba(0, 0, 0, 0.1);
    border-radius: 20px;
    background-color: #FFFFFF;
    padding: 10px;
    width: 50%;
    margin-left: 15px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    .wrraper-top{
        display: flex;
        margin-bottom: 10px;
        justify-content: flex-end;
        align-items: flex-end;

    }
    .element_1{
        flex: 1;
        display: flex;
        justify-content: center;
        align-items: flex-start;
        flex-direction: column;

    }
    .element_2{
        flex: 1;
        display: flex;
        justify-content: center;
        align-items: flex-start;
        flex-direction: column;
        margin: 0px 7px;

    }
    .element_3{
        flex:1;
        display: flex;
        justify-content: center;
        align-items: flex-start;
        flex-direction: column;

    }
    .element_4{
        flex: 1;
        display: flex;
        justify-content: center;
        align-items: center;
        .element_5{
            flex:1;
            display: none;
        }
    }
    @media screen and (max-width:750px) {
        width: 100%;
        margin-left: 90px;
        padding:2px;
        .element_2{
            align-items: flex-end;
            .span2{
               padding-right: 88px;
            }
        }
        .element_3{
            display: none;
        }
       .element_4{ .element_5{
            display: flex;
            justify-content: flex-start;
            align-items: flex-start;
            flex-direction: column;
        }
    }
        
    }
    @media screen and (max-width:360px){
        width: 100%;
        margin-left: 90px;
        .element_2{
            align-items: flex-end;
            .span2{
               padding-right: 65px;
            }
        }
        .element_3{
            display: none;
        }
       .element_4{ .element_5{
            display: flex;
            justify-content: flex-start;
            align-items: flex-start;
            flex-direction: column;
        }
    }
        

    }
    @media screen and (max-width:414px){
        width: 100%;
        margin-left: 90px;
        .element_2{
            align-items: flex-end;
            .span2{
               padding-right: 65px;
            }
        }
        .element_3{
            display: none;
        }
       .element_4{ .element_5{
            display: flex;
            justify-content: flex-start;
            align-items: flex-start;
            flex-direction: column;
        }
    }

    }
}

